function goToPositionGenerator(location, condition) {
  return () => {
    console.log('global.playerData.iDoorOpen', global.playerData.iDoorOpen)
    console.log('Test', typeof condition === 'undefined', condition);
    if (typeof condition === 'undefined' || condition) {
      global.playerData.playerPosition = location;
    }
  }
}

function goToPosition(location) {
  global.playerData.playerPosition = location;
}

